//@ts-check
import template from "./home.html";
import "./home.scss";
import { UserService } from '../../services/user.service'
import angular from 'angular'

class HomeController {
  constructor(UserService, Restangular, PagerService) {
    this.name = "home";
    this.Restangular = Restangular
    this.PagerService = PagerService
    this.UserService = UserService
    this.getTasklists()
    this.tasklist = {}
    this.tasklist.tasks = []
    this.pager = {}
    
  }

  setPage(page) {
    if (page < 1 || page > this.PagerService.totalPages && this.PagerService.totalPages !== 0) {
      return;
    }
      // get pager object from service
    this.PagerService.getPager(this.tasklists2.length, page, 4);
      // get current page of items
    this.tasklists = this.tasklists2.slice(this.PagerService.startIndex, this.PagerService.endIndex + 1);
  }

  getTasklists() {
    this.UserService.getTasklists()
    .then(tasklists => {
        this.tasklists2 = tasklists
        // this.tasklists = tasklists
        this.setPage(1)
    }, err => {
        console.log(err)
    })
  }

  createTasklist() {
    if(!this.tasklist.title){return}
    this.UserService.createTasklist(this.tasklist)
      .then(tasklist => {
        this.tasklists2.push(tasklist)
        this.setPage(this.PagerService.currentPage)
      }, err => {
          console.log(err)
      })
  }

  prepareDelete(tasklist, i) {
    this.tasklist2 = tasklist
    this.i = i
  }

  deleteTasklist(tasklist, i) {
    this.UserService.deleteTasklist(this.tasklist2)
      .then(res => {
          this.tasklists2.splice(this.i, 1) 
          this.setPage(this.PagerService.currentPage)
      },err=> {
        console.log(err)
      })
  }

  addTask(i, newTaskMessage) {
    if(!newTaskMessage){return}
    let tasklist = this.Restangular.copy(this.tasklists[i])
    let task = {priority: 'medium', isDone: false, message: ''}
    task.message = newTaskMessage
    tasklist.tasks.push(task)
    this.UserService.addTask(tasklist)
        .then(res => {
          this.tasklists[i] = res
        },err => {
          console.log(err)
        })
  }

  updateTask(tasklist) {
    this.UserService.updateTask(tasklist)
      .then(res => {
      },err => {
        console.log(err)
      })
  }

  deleteTask(tasklist, i) {
    let tasklistCopy = this.Restangular.copy(tasklist)
    tasklistCopy.tasks.splice(i, 1)
    this.UserService.deleteTask(tasklistCopy)
      .then(res => {
        let j = this.tasklists.indexOf(tasklist)
        this.tasklists[j] = res
      },err => {
        console.log(err)
      })
  }

}


HomeController.$inject = ["UserService", "Restangular", "PagerService"];

let homeComponent = {
  bindings: {},
  template,
  controller: HomeController
};

export default angular.module("home", [])
  .component("home", homeComponent).name;

