import { statesArray } from './app.states'

export default angular.module('routing', [])
    .config(['$uiRouterProvider', ($uiRouter)=>{
        //console logs state transition
        //$uiRouter.trace.enable(1);
        //if the user enters unknown route redirect 
        const $urlService = $uiRouter.urlService;
        $urlService.rules.otherwise({ state: 'home' });
        const $stateRegistry = $uiRouter.stateRegistry;
        for(let i=0; i<statesArray.length; i++){
            $stateRegistry.register(statesArray[i]);
        }
    }])