import template from "./app.html";
import "./app.scss";
import { AuthService } from './services/auth.service'


class AppController {
  constructor($transitions, AuthService) {
    this.name = "App";
    this.state = { name: "ephraim" };
    this.AuthService = AuthService
    $transitions.onStart({}, (transition)=>{
      if(this.AuthService.isProtected(transition.to().name) && !this.AuthService.isLoggedIn()){
        return transition.router.stateService.target('login');
      }else if(!this.AuthService.isProtected(transition.to().name) && this.AuthService.isLoggedIn()){
        return transition.router.stateService.target('home')
      }
    })
  }
}

AppController.$inject = ['$transitions', 'AuthService']


let appComponent = {
  bindings: {},
  template,
  controller: AppController
};

export default appComponent;
