//@ts-check
import { protectedRoutes } from '../app.states'

export class AuthService {
  constructor(Restangular, $rootScope, $state) {
    this.Restangular = Restangular;
    this.$rootScope = $rootScope
    this.$state = $state
  }


  fetchUser() {
    this.Restangular.one(`Users`,localStorage.getItem('userId'))
      .get({access_token: localStorage.getItem('token')})
      .then(res => {
      this.user = res
        console.log(res)
      },err => {
        console.log(err)
      })
  }

  getUser() {
    return this.user
  }

  logIn(user) {
    return this.Restangular.all('Users/login')
      .post(user)
      .then(auth => {
        localStorage.setItem('token', auth.id)
        localStorage.setItem('userId', auth.userId)
        this.fetchUser()
        this.$state.go('home')
      })
  }

  isLoggedIn() {
    return localStorage.getItem('token')
  }

  signup(user) {
    return this.Restangular.all('Users')
      .post(user)
      .then(auth => {
        this.$state.go('login')
      })
  }

  logout() {
    this.Restangular.all('users/logout')
    .post('', {access_token: localStorage.getItem('token')})
    localStorage.removeItem('token')
    localStorage.removeItem('userId')
    this.$state.go('login')
  }
  
  isProtected(route) {
    for(let i=0; i<protectedRoutes.length; i++){
      if(route === protectedRoutes[i]){        
        return true
      }      
    }
    return false
  }
}

 AuthService.$inject = ["Restangular", "$rootScope", '$state'];
