//@ts-check
export class UserService {
    constructor(Restangular) {
      this.Restangular = Restangular;
    }

    getTasklists() {
       return this.Restangular.all(`users/${localStorage.getItem('userId')}/tasklists`)
            .getList({access_token: localStorage.getItem('token')})
    }

    createTasklist(tasklist) {
        return this.Restangular.all(`users/${localStorage.getItem('userId')}/tasklists`)
            .post(tasklist, {access_token: localStorage.getItem('token')})
            
    }

    deleteTasklist(tasklist) {
        return this.Restangular.one('users', localStorage.getItem('userId')).one('tasklists', tasklist.id)
            .remove({access_token: localStorage.getItem('token')})
    }

    addTask(tasklist) {
        return tasklist.put({access_token: localStorage.getItem('token')})
    }

    updateTask(tasklist) {
        return tasklist.put({access_token: localStorage.getItem('token')})
    }
    
    deleteTask(tasklist) {
        console.log(tasklist)
        return tasklist.put({access_token: localStorage.getItem('token')})
    }


  }
  
   UserService.$inject = ["Restangular"];
  