//@ts-check
import _ from 'lodash'
export class PagerService {
  constructor(Restangular) {
    this.Restangular = Restangular;
    this.pages = []
  }

  getPager(totalItems, currentPage, pageSize) {
    // default to first page
    this.currentPage = currentPage || 1;

    // default page size is 10
    pageSize = pageSize || 10;

    // calculate total pages
    this.totalPages = Math.ceil(totalItems / pageSize);

    let startPage, endPage;
    if (this.totalPages <= 10) {
        // less than 10 total pages so show all
        startPage = 1;
        endPage = this.totalPages;
    } else {
        // more than 10 total pages so calculate start and end pages
        if (this.currentPage <= 6) {
            startPage = 1;
            endPage = 10;
        } else if (this.currentPage + 4 >= this.totalPages) {
            startPage = this.totalPages - 9;
            endPage = this.totalPages;
        } else {
            startPage = this.currentPage - 5;
            endPage = this.currentPage + 4;
        }
    }

    // calculate start and end item indexes
    this.startIndex = (this.currentPage - 1) * pageSize;
    this.endIndex = Math.min(this.startIndex + pageSize - 1, totalItems - 1);

    // create an array of pages to ng-repeat in the pager control
    this.pages = _.range(startPage, endPage + 1);
    }

    lastPages() {
        if (this.totalPages === 0) {return false}
        if (this.currentPage === this.totalPages){return false}
        return true
    }
}

PagerService.$inject = ["Restangular"];
